<template>
  <div class="add-address">
    <nav-head title="编辑收货信息" FixedOrTransparency="isFixed NoTransparency">
      <span slot="right" class="question-icon nav-right" @click="saveEdit">确定</span>
    </nav-head>
    <div class="padding-0-25">
      <input type="text" maxlength="10" v-model="user_name" placeholder="请输入姓名">
      <input type="text" maxlength="11" v-model="user_phone" placeholder="请输入手机号">
      <select-province v-bind:list="item" @setProvince="setProvince"></select-province>
      <textarea maxlength="30" rows="2" placeholder="请输入详细地址：如道路、门牌号、小区、楼栋号、单元室等" v-model="address"></textarea>
      <input type="text" maxlength="15" placeholder="请输入QQ号码" v-model="user_qq">
      <input type="text" disabled placeholder="设为默认地址">
      <mt-switch v-model="is_default" class="btn-switch"></mt-switch>
    </div>
  </div>
</template>
<script>
import NavHead from '@/components/NavHead.vue'
import CaptchaButton from '@/components/CaptchaButton'
import SelectProvince from '../components/SelectProvince.vue'
import { Button, Switch, Toast } from 'mint-ui'
import Util from '@/tool/util'
import { mapActions } from 'vuex'
import { EDIT_ADDRESS } from '@/vuex/Mine'

export default {
  name: 'editAddress',
  data () {
    return {
      item: this.$route.query.list,
      user_name: this.$route.query.list.user_name,
      user_phone: this.$route.query.list.user_phone,
      address: this.$route.query.list.address,
      id: this.$route.query.list.id,
      user_qq: this.$route.query.list.user_qq,
      is_default: this.$route.query.list.is_default === 1,
      province_name: this.$route.query.list.province_name,
      city_name: this.$route.query.list.city_name,
      county_name: this.$route.query.list.county_name,
      province: this.$route.query.list.province,
      city: this.$route.query.list.city,
      county: this.$route.query.list.county
    }
  },
  methods: {
    saveEdit () {
      if (!this.user_name) {
        Toast('请输入姓名!')
        return
      }
      if (!Util.isMobile(this.user_phone)) {
        Toast('请输入正确的手机号!')
        return
      }
      if (!this.province_name && !this.province) {
        Toast('请选择省份!')
        return
      }
      if (!this.city_name && !this.city) {
        Toast('请选择城市!')
        return
      }
      if (!this.county_name && !this.county) {
        Toast('请选择区/县!')
        return
      }
      if (!this.address) {
        Toast('请输入详细地址!')
        return
      }
      if (!Util.isQQ(this.user_qq)) {
        Toast('请输入正确的QQ号码!')
        return
      }
      if (this.user_name && this.user_phone && this.province && this.city && this.county && this.address && this.user_qq) {
        this.editAddress({
          id: this.id,
          user_name: this.user_name,
          user_phone: this.user_phone,
          user_qq: this.user_qq,
          province: this.province,
          city: this.city,
          county: this.county,
          address: this.address,
          is_default: this.is_default ? 1 : 0
        })
      }
    },
    setProvince (obj) {
      this.province_name = obj.province_name
      this.city_name = obj.city_name
      this.county_name = obj.county_name
      this.province = obj.province
      this.city = obj.city
      this.county = obj.county
    },
    ...mapActions({
      editAddress: EDIT_ADDRESS
    })
  },
  components: {
    SelectProvince,
    NavHead,
    CaptchaButton,
    [Button.name]: Button,
    [Switch.name]: Switch
  }
}
</script>
<style scoped lang="scss">
  .add-address {
    text-align: center;
    background-color: $background-light-color;
    min-height: 100%;
    position: absolute;
    width: 100%;
    > div.padding-0-25 {
      padding: 0 25px;
      input[type=text] {
        border: none;
        outline: none;
        width: 100%;
        font-size: 16px;
        height: 56px;
        line-height: 56px;
        margin-bottom: 10px;
        color: $font-color;
        border-bottom: 1px solid $border-color;
        background: none;
        border-radius: 0;
      }
      textarea {
        display: block;
        width: 100%;
        border-radius: 0;
        background-color: $background-light-color;
        color: $font-color;
        font-size: 16px;
        border: none;
        resize: none;
        line-height: 26px;
        outline: none;
        padding: 10px 0;
        border-bottom: 1px solid $border-color;
      }
      .btn-switch {
        float: right;
        margin-top: -55px;
        -webkit-transform: scale(0.8);
        -moz-transform: scale(0.8);
        -o-transform: scale(0.8);
        transform: scale(0.8);
      }
    }
  }
</style>
